@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap');
@import "../scss-variables";
@import "../style-variables";

.ASWidget {
    display: block;
    width: 100%;
    max-width: 100%;
    height: auto;
    overflow: hidden;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    line-height: normal;

    * {
        box-sizing: border-box;
        font-family: 'Inter', sans-serif;
        font-style: normal;
        font-weight: normal;
        line-height: normal;
    }


    //- Options
    &__wrapper {
        display: flex;
        flex-direction: column;
        gap: 20px;
        width: 100%;
        height: auto;
        margin: 0;
        padding: 20px 10px;

        @media (min-width: $md) {
            gap: 40px;
            padding: 40px 20px;
        }
    }


    //- Header
    &__header {
        display: flex;
        flex-wrap: no-wrap;
        justify-content: flex-end;
        align-items: center;
        gap: 10px;
    }

    &__header-title {
        display: var(--display-title);
        margin: 0;
        padding: 0;
        color: var(--color-title);
        font-size: 22px;
        font-weight: 500;
        width: 100%;
        height: auto;

        @media (min-width: $md) {
            font-size: 34px;
        }
    }

    &__header-link {
        flex-shrink: 0;
        display: var(--display-link-all);
        position: relative;
        width: auto;
        height: auto;
        color: var(--color-link-all);
        text-align: center;
        font-size: 10px;
        font-weight: 600;
        text-decoration: none;
        transition: color .1s linear;

        &::before {
            content: "";
            display: block;
            position: absolute;
            left: 0;
            bottom: -1px;
            width: 100%;
            height: 1px;
            opacity: 0.8;
            background-color: var(--color-link-all);
            transition: background-color .1s linear;
        }

        @media (min-width: $md) {
            font-size: 14px;
            font-weight: 500;
        }

        @media (min-width: $md) {
            &:hover {
                color: var(--color-link-all-hover);

                &::before {
                    background-color: var(--color-link-all-hover);
                }
            }
        }
    }


    //- Cards
    &__card-list {
        display: grid;
        grid-template-columns: var(--cards-count-sm);
        gap: 10px;
        list-style: none;
        margin: 0;
        padding: 0;

        @media (min-width: $sm) {
            grid-template-columns: var(--cards-count-md);
        }

        @media (min-width: $lg) {
            gap: 20px;
            grid-template-columns: var(--cards-count-lg);
        }

        @media (min-width: $xl) {
            grid-template-columns: var(--cards-count-xl);
        }
    }

    &__card-item {
        display: block;
        position: relative;
        width: 100%;
        height: auto;
    }

    &__card {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        width: 100%;
        height: auto;
        container-type: inline-size;
        container-name: widgetCard;
    }

    &__card-img {
        display: block;
        position: relative;
        width: 100%;
        height: auto;
        margin: 0 0 10px 0;
        padding: 0;

        &::before {
            content: "";
            display: block;
            position: relative;
            width: 100%;
            height: auto;
            padding-bottom: 100%;
        }

        img {
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__card-code {
        display: block;
        width: 100%;
        height: auto;
        margin: 0 0 10px 0;
        padding: 0;
        color: var(--color-code);
        font-size: 10px;
    }

    &__card-name {
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 100%;
        height: 34px;
        margin: 0 0 10px 0;
        padding: 0;
        color: var(--color-name);
        font-size: 14px;
    }

    &__card-price {
        display: block;
        width: 100%;
        height: auto;
        margin: 0 0 10px 0;
        padding: 0;
        color: var(--color-price);
        font-size: 14px;
        font-weight: 700;
    }

    &__card-link {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 50px;
        border: 1px solid var(--color-button);
        border-radius: 2px;
        background: var(--color-button);
        color: var(--color-button-text);
        text-align: center;
        font-size: 14px;
        font-weight: 500;
        text-decoration: none;
        cursor: pointer;
        transition: color .1s linear, background-color .1s linear;

        @media (min-width: $xl) {
            &:hover {
                background-color: var(--color-button-hover);
                color: var(--color-button-text-hover);
            }
        }
    }
}


@container widgetCard (min-width: 260px) {
    .ASWidget {
        &__card-img {
            margin-bottom: 20px;
        }

        &__card-code {
            font-size: 12px;
        }

        &__card-name {
            height: 40px;
            margin-bottom: 20px;
            font-size: 16px;
        }

        &__card-price {
            margin-bottom: 20px;
            font-size: 16px;
        }

        &__card-link {
            height: 60px;
        }
    }
}