.widget {
    --font-family: 'Inter', sans-serif; // семейство шрифтов
    --display-title: block; // отображать заголовок виджета
    --color-title: #262626; // цвет заголовка виджета
    --display-link-all: dispaly;  // отображать ссылку Смотреть все
    --color-link-all: #262626; // цвет ссылки Смотреть все
    --color-link-all-hover: #8C8C8C; // цвет ссылки Смотреть все при ховере
    --cards-count-sm: repeat(2, 1fr); // количество карточек на мобильных
    --cards-count-md: repeat(3, 1fr); // количество карточек на планшетах
    --cards-count-lg: repeat(4, 1fr); // количество карточек на стандартных экранах
    --cards-count-xl: repeat(5, 1fr); // количество карточек на широких экранах
    --color-code: #8C8C8C; // цвет артикула
    --color-name: #262626; // цвет названия товара
    --color-price: #262626; // цвет цены
    --color-button: #262626; // цвет кнопки Смотерть товар
    --color-button-hover: #ffffff; // цвет кнопки Смотреть товар при ховере
    --color-button-text: #ffffff; // цвет текста Смотреть товар
    --color-button-text-hover: #262626; // цвет текста Смотреть товар при ховере
}